import React, { Component } from 'react'
import { Input, Button, Table, Modal } from "antd";
import Opertion from "../component/operation"
const { confirm } = Modal;
export default class index extends Component {
   state={columns: [
    {
      title: "",
      dataIndex: "name",
      className: "listOne"
    },
    {
      title: "",
      dataIndex: "xh"
    },
    {
      title: "",
      dataIndex: "bm"
    },
    {
      title: "",
      dataIndex: "zw",
    },
    {
      title: "",
      dataIndex: "sj"
    },
    {
      title: "",
      dataIndex: "rzsj"
    },{
      title:"",
      dataIndex:"sjh"
    },{
      title:"",
      dataIndex:"bz"
    }
  ],
  datas: [
    {
      key: "1",
      name: "John Brown",
      xh: 32,
      bm: "",
      zw:"",
      sj:"",
      rzsj:"2016.10.9",
      sjh:"152312131222",
      bz:"",
    }
  ]};
  render() {
    let {columns,datas}=this.state;
    return (
      <div className="yggl_wrap">
        <h2></h2> 
        <div className="yggl_serch">
            <Input className="" size="small" placeholder="small "/><Button></Button>
        </div>
        <div className="operation">
        <Opertion 
          showConfirm={this.showConfirm.bind(this)}
          addfn={this.addfn.bind(this)}
          closes={this.closes.bind(this)}>
           
          </Opertion>
        </div>
        <div className="yggl_list">
            <Table columns={columns} dataSource={datas} bordered  size="middle" />
        </div>
      </div>
    )
  }
    //
    showConfirm = () => {
      confirm({
        title: "",
        content: "?",
        okText:"",
        cancelText:"",
        onOk() {
          console.log("OK");
        },
        onCancel() {
          console.log("Cancel");
        }
      });
    };
    addfn(){
      console.log("")
    }
    closes(){
      console.log("")
    }
}
